
import {
  RECEIVE_SHOPS,
  RECEIVE_CATEGORYS,
  RECEIVE_ADDRESS,
  RECEIVE_RECORD_USER,
  RECEIVE_USER,
  RESET_USER_INFO,
  GET_SHOP_GOODS,
  GET_SHOP_RATINGS,
  GET_SHOP_INFO,
  UPDATE_CART,
  CLEAR_CART,
  ADD_USER
} from './mutations-types'

import {
  reqAddress,
  reqFoodCategorys,
  reqShops,
  reqUserInfo,
  reqLogout,
  reqShopGoods,
  reqShopRatings,
  reqShopInfo
} from '../api'

export  default {
  //

  async getAddress({commit,state}){
    //ajax
    const geoHash = state.latitude + ',' + state.longitude
    const result = await reqAddress(geoHash)  //address,acync/await
    console.log(result)
    if(result.code === 0){
      const address = result.data
      commit(RECEIVE_ADDRESS, {address})
    }

  },
  //
  async getCategory ({commit}){
    const result = await reqFoodCategorys()

    if(result.code === 0){
      const categorys = result.data
      commit(RECEIVE_CATEGORYS, {categorys})
    }
  },
  //
  async getShopList({commit, state}) {
    const {longitude, latitude} = state
    const result = await reqShops(longitude, latitude)
    if (result.code === 0) {
      const shops = result.data
      commit(RECEIVE_SHOPS, {shops})
    }
  },

  getCategorys({commit}, categorys){
    commit(RECEIVE_ADDRESS, {categorys})
  },
  getShops({commit}, shops){
    commit(RECEIVE_SHOPS, {shops})
  },

  //
  recordUser({commit}, userInfo) {
    commit(RECEIVE_RECORD_USER, {userInfo})
  },
  //
  updateCart({commit}, cartInfo) {
    commit(UPDATE_CART, {cartInfo})
  },
  //
  clearCart({commit}){
    commit(CLEAR_CART)
  },
  //
  addUser({commit},newUser) {
    commit(ADD_USER, {newUser})
  },
  //
  async getUserInfo({commit}) {
    // const result = await reqUserInfo()
    // if(result.code === 0){
    //   const userInfo = result.data
    //   commit (RECEIVE_USER, {userInfo})
    // }
  },

  //,,ajax
  // async reqLogout({commit}) {
    // const result = await reqLogout()
    // if(result.code === 0){
    //   commit (RESET_USER_INFO)
    // }
  // },

  //,state.userInfo
  reqLogout({commit}){
    commit(RESET_USER_INFO)
  },

  //shop

  async getShopGoods ({commit}){
    const result = await reqShopGoods()
    if(result.code === 0){
      const shopGoods = result.data
      commit(GET_SHOP_GOODS, {shopGoods})
    }
  },

  async getShopRatings ({commit}) {
    const result = await reqShopRatings()
    if(result.code === 0){
      const shopRatings = result.data
      commit(GET_SHOP_RATINGS, {shopRatings})
    }
  },
  //
  async getShopInfo ({commit}) {
    const result = await reqShopInfo()
    if(result.code === 0) {
      const shopInfo = result.data
      commit(GET_SHOP_INFO, {shopInfo})
    }
  }
}




